import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewFighter from '@/components/NewFighter'
import ViewFighter from '@/components/ViewFighter'
import EditFighter from '@/components/EditFighter'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-fighter',
      component: NewFighter
    },
    {
      path: '/edit/:fighter_id',
      name: 'edit-fighter',
      component: EditFighter
    },
    {
      path: '/:fighter_id',
      name: 'view-fighter',
      component: ViewFighter
    }
  ]
})



